.DataAnalyticsProjects {
    width: 100vw;
    height: 100vh;
    overflow: scroll;
    overflow-x: hidden;
    scrollbar-width: thin;
    scrollbar-color: gold deepskyblue;
}

.DataAnalyticsProjects > h1 {
    color: white;
    font-size: 2.25rem;
    font-weight: 500;
    text-align: center;
    margin: 4rem 0;
}

.exploration-sql {
    display: flex;
    justify-content: center;
    align-items: center;
}

.image--exploration-sql {
    max-width: 26rem;
    margin-right: 2rem;
    border-radius: 0.5rem;
}

.text-container {
    max-width: 26rem;
    margin-left: 2rem;
    text-align: block;
}

.exploration-sql-title {
    white-space: pre-line;
    font-size: 1.5rem;
    color: white;
}

.exploration-sql-description {
    white-space: pre-line;
    font-size: 1.1rem;
    line-height: 1.5rem;
    color: white;
}

.data-cleaning {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 4rem;
}

.image--data-cleaning {
    max-width: 26rem;
    margin-right: 2rem;
    border-radius: 0.5rem;
}

.data-cleaning-title {
    white-space: pre-line;
    font-size: 1.5rem;
    color: white;
}

.data-cleaning-description {
    white-space: pre-line;
    font-size: 1.1rem;
    line-height: 1.5rem;
    color: white;
}

.tableau {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 4rem;
}

.image--tableau {
    max-width: 26rem;
    margin-right: 2rem;
    border-radius: 0.5rem;
}

.tableau-title {
    white-space: pre-line;
    font-size: 1.5rem;
    color: white;
}

.tableau-description {
    white-space: pre-line;
    font-size: 1.1rem;
    line-height: 1.5rem;
    color: white;
}

.excel {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 4rem;
}

.image--excel {
    max-width: 26rem;
    margin-right: 2rem;
    border-radius: 0.5rem;
}

.excel-title {
    white-space: pre-line;
    font-size: 1.5rem;
    color: white;
}

.excel-description {
    white-space: pre-line;
    font-size: 1.1rem;
    line-height: 1.5rem;
    color: white;
}

.more-projects-data {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 12rem 0 8rem 0;
}

.projects-data {
    font-size: 1.1rem;
    color: white;
    margin-right: 0.25rem;
}

.github-data > a {
    font-size: 2rem;
    color: white;
    margin-left: 0.25rem;
}

.github-data > a:hover {
    color: gold;
}

@media screen and (max-width: 720px) {
    .exploration-sql {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        margin-top: 2rem;
    }

    .image--exploration-sql {
        margin: 0 1.5rem;
    }

    .data-cleaning {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        margin-top: 2rem;
    }

    .image--data-cleaning {
        margin: 0 1.5rem;
    }

    .tableau {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        margin-top: 2rem;
    }

    .image--tableau {
        margin: 0 1.5rem;
    }

    .excel {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        margin-top: 2rem;
    }

    .image--excel {
        margin: 0 1.5rem;
    }

    .more-projects-data {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        margin: 6rem 1.5rem 8rem 1.5rem;
    }
}

@media screen and (max-width: 420px) {
    .image--exploration-sql {
        max-width: 20rem;
        margin: 0 1.5rem;
    }

    .image--data-cleaning {
        max-width: 20rem;
        margin: 0 1.5rem;
    }

    .image--tableau {
        max-width: 20rem;
        margin: 0 1.5rem;
    }

    .image--excel {
        max-width: 20rem;
        margin: 0 1.5rem;
    }

    .text-container {
        max-width: 20rem;
    }
}
