.Navbar {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 3.75rem;
    overflow: hidden;
    background-image: linear-gradient(
        to right,
        deepskyblue,
        mediumblue,
        indigo
    );
}

.navbar-container {
    display: flex;
}

li {
    list-style: none;
}

.navlink {
    text-decoration: none;
    color: white;
    font-size: 1.1rem;
    padding: 0 1.5rem;
}

.navlink:hover {
    color: gold;
}

.navlink:focus {
    color: gold;
}

@media screen and (max-width: 720px) {
    .navbar-container {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .navlink {
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 1rem;
        padding: 1rem;
    }
}

@media screen and (max-width: 420px) {
    .navbar-container {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .navlink {
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 1rem;
        padding: 0.5rem;
    }
}
