.Contact {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
}

.Contact > h1 {
    color: white;
    font-size: 2.25rem;
    font-weight: 500;
}

.email {
    padding-top: 1rem;
}

.email-icon {
    font-size: 1.5rem;
}

.email-font > a {
    text-decoration: none;
    color: white;
    font-size: 1.1rem;
    margin-left: 1rem;
}

.github > a {
    display: inline-block;
    font-size: 3rem;
    color: white;
    padding: 1rem 1rem 0 0;
}

.linkedin > a {
    display: inline-block;
    font-size: 3rem;
    color: white;
    padding: 1rem 0 0 1rem;
}

.email-font > a:hover {
    color: gold;
}

.github > a:hover {
    color: gold;
}

.linkedin > a:hover {
    color: gold;
}
