.SoftwareDevelopmentProjects {
    width: 100vw;
    height: 100vh;
    overflow: scroll;
    overflow-x: hidden;
    scrollbar-width: thin;
    scrollbar-color: gold deepskyblue;
}

.SoftwareDevelopmentProjects > h1 {
    color: white;
    font-size: 2.25rem;
    font-weight: 500;
    text-align: center;
    margin: 4rem 0;
}

.quizzical {
    display: flex;
    justify-content: center;
    align-items: center;
}

.image--quizzical {
    max-width: 13rem;
    margin-right: 2rem;
    border-radius: 0.5rem;
}

.text-container {
    margin-left: 2rem;
    text-align: block;
    max-width: 26rem;
}

.quizzical-title {
    white-space: pre-line;
    font-size: 1.5rem;
    color: white;
}

.quizzical-description {
    white-space: pre-line;
    font-size: 1.1rem;
    line-height: 1.5rem;
    color: white;
}

.drum-machine {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 4rem;
}

.image--drum-machine {
    max-width: 26rem;
    margin-right: 2rem;
    border-radius: 0.5rem;
}

.drum-machine-title {
    white-space: pre-line;
    font-size: 1.5rem;
    color: white;
}

.drum-machine-description {
    white-space: pre-line;
    font-size: 1.1rem;
    line-height: 1.5rem;
    color: white;
}

.react-calculator {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 4rem;
}

.image--react-calculator {
    max-width: 13rem;
    margin-right: 2rem;
    border-radius: 0.5rem;
}

.calculator-title {
    white-space: pre-line;
    font-size: 1.5rem;
    color: white;
}

.calculator-description {
    white-space: pre-line;
    font-size: 1.1rem;
    line-height: 1.5rem;
    color: white;
}

.pomodoro-timer {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 4rem;
}

.image--pomodoro-timer {
    max-width: 26rem;
    margin-right: 2rem;
    border-radius: 0.5rem;
}

.pomodoro-title {
    white-space: pre-line;
    font-size: 1.5rem;
    color: white;
}

.pomodoro-description {
    white-space: pre-line;
    font-size: 1.1rem;
    line-height: 1.5rem;
    color: white;
}

.more-projects-software {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 12rem 0 8rem 0;
}

.projects-software {
    font-size: 1.1rem;
    color: white;
    margin-right: 0.25rem;
}

.github-software > a {
    font-size: 2rem;
    color: white;
    margin-left: 0.25rem;
}

.github-software > a:hover {
    color: gold;
}

@media screen and (max-width: 720px) {
    .text-container {
        margin-top: 1.5rem;
        max-width: 25rem;
    }

    .quizzical {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        margin-top: 2rem;
    }

    .image--quizzical {
        margin: 0 1.5rem;
    }

    .drum-machine {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        margin-top: 2rem;
    }

    .image--drum-machine {
        margin: 0 1.5rem;
    }

    .react-calculator {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        margin-top: 2rem;
    }

    .image--react-calculator {
        margin: 0 1.5rem;
    }

    .pomodoro-timer {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        margin-top: 2rem;
    }

    .image--pomodoro-timer {
        margin: 0 1.5rem;
    }

    .more-projects-software {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        margin: 6rem 1.5rem 8rem 1.5rem;
    }
}

@media screen and (max-width: 420px) {
    .image--quizzical {
        max-width: 10rem;
        border-radius: 0.5rem;
    }

    .image--drum-machine {
        max-width: 20rem;
        border-radius: 0.5rem;
    }

    .image--react-calculator {
        max-width: 10rem;
        border-radius: 0.5rem;
    }

    .image--pomodoro-timer {
        max-width: 20rem;
        border-radius: 0.5rem;
    }

    .text-container {
        max-width: 20rem;
    }
}
